

















h1,
h2 {
  font-weight: normal;
}

.header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  max-width: 400px;
  width: 100%;
  background: #41B883;
  border-radius: 10px;
  -webkit-box-shadow: 0 0px 1px rgba(0, 0, 0, 0.5), 0 1px 2px rgba(0, 0, 0, 0.48);
          box-shadow: 0 0px 1px rgba(0, 0, 0, 0.5), 0 1px 2px rgba(0, 0, 0, 0.48);
}
