
















































.helper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  font-size: 20px;
  font-weight: 400;
  line-height: 30px;
  padding: 5px 0;
  margin-top: 15px;
}

.left
.tabs,
.clear {
  padding: 0 5px;
}

.tabs {
  width: 200px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

.tabs .middle.actived {
  display: inline-block;
  border: 1px solid #bd3737;
  border-radius: 5px;
  padding: 0 5px;
  cursor: pointer;
}

.clear {
  cursor: pointer;
}
